.container__home {
    text-align: center;
}

.title {
    color: #fff;
}

.list {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    -webkit-box-pack: center;
    justify-content: center;
    align-items: center;
}

.link {
    border: 1px solid #fff;
    border-radius: 10px;
    box-shadow: 0 0 2px 1px rgba(221, 221, 221, 0.5);
    width: 100%;
    max-width: 240px;
    margin: 0.5em;
}

.img {
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.filmName {
    color:#fff;
}